
.overseer-panel {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.section {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  padding: 12px;
  border: 1px solid #3a3a3a;
}

.section-title {
  margin: 0 0 12px 0;
  font-size: 14px;
  font-weight: 600;
  color: #d4a84b;
  display: flex;
  align-items: center;
  gap: 8px;

  i {
    font-size: 14px;
  }
}

.overseer-header {
  display: flex;
  align-items: center;
  gap: 16px;
}

.avatar {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #1a4a6e 0%, #2d5a7e 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #d4a84b;

  i {
    font-size: 28px;
    color: #d4a84b;
  }
}

.basic-info {
  flex: 1;
}

.name {
  font-size: 20px;
  font-weight: bold;
  color: #e0e0e0;
  margin-bottom: 4px;
}

.location {
  font-size: 12px;
  color: #888;

  i {
    margin-right: 4px;
    color: #4fc3f7;
  }
}

.action-points {
  text-align: center;
  padding: 8px 16px;
  background: rgba(212, 168, 75, 0.1);
  border-radius: 6px;
  border: 1px solid #d4a84b;
}

.ap-label {
  font-size: 10px;
  color: #888;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.ap-value {
  font-size: 20px;
  font-weight: bold;
  margin: 2px 0;

  .current {
    color: #ffd700;
  }

  .separator {
    color: #666;
    margin: 0 2px;
  }

  .max {
    color: #888;
  }
}

.ap-pips {
  display: flex;
  justify-content: center;
  gap: 4px;
}

.pip {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #333;
  border: 2px solid #555;

  &.filled {
    background: #ffd700;
    border-color: #ffa000;
    box-shadow: 0 0 6px rgba(255, 215, 0, 0.5);
  }
}

.special-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.special-item {
  display: flex;
  align-items: center;
  gap: 12px;
}

.special-letter {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #2a2a2a;
  border-radius: 4px;
  font-size: 14px;
  font-weight: bold;
  color: #d4a84b;
  border: 1px solid #444;
}

.special-info {
  flex: 1;
}

.special-name {
  font-size: 11px;
  color: #888;
  margin-bottom: 2px;
}

.special-bar {
  height: 6px;
  background: #2a2a2a;
  border-radius: 3px;
  overflow: hidden;
}

.special-fill {
  height: 100%;
  transition: width 0.3s ease;
}

.special-value {
  width: 24px;
  text-align: right;
  font-size: 16px;
  font-weight: bold;
}

.luck-bonus {
  margin-top: 12px;
  padding: 8px;
  background: rgba(76, 175, 80, 0.1);
  border-radius: 4px;
  font-size: 12px;
  color: #4caf50;
  text-align: center;

  i {
    margin-right: 6px;
  }
}

.empty-hint {
  text-align: center;
  padding: 20px;
  color: #666;
  font-size: 12px;
  font-style: italic;
}

.perk-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.perk-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 4px;
  border: 1px solid #333;
}

.perk-icon {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(212, 168, 75, 0.2);
  border-radius: 50%;

  i {
    font-size: 14px;
    color: #d4a84b;
  }
}

.perk-info {
  flex: 1;
}

.perk-name {
  font-size: 12px;
  font-weight: 600;
  color: #e0e0e0;
}

.perk-effect {
  font-size: 10px;
  color: #888;
}
