
.world-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background: linear-gradient(90deg, #1a4a6e 0%, #2d5a7e 100%);
  border-bottom: 2px solid #4a4a4a;
}

.header-left {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.shelter-name {
  font-size: 18px;
  font-weight: bold;
  color: #d4a84b;
  display: flex;
  align-items: center;
  gap: 8px;

  i {
    font-size: 20px;
  }
}

.disaster-info {
  display: flex;
  gap: 12px;
  font-size: 12px;
}

.disaster-type {
  color: #ff6b35;
  font-weight: 500;
}

.timeline {
  color: #a0a0a0;
}

.header-right {
  text-align: right;
}

.datetime {
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-size: 12px;
  color: #c0c0c0;

  i {
    margin-right: 4px;
    opacity: 0.7;
  }
}
