
.shelter-status {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.section {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  padding: 12px;
  border: 1px solid #3a3a3a;
}

.section-title {
  margin: 0 0 12px 0;
  font-size: 14px;
  font-weight: 600;
  color: #d4a84b;
  display: flex;
  align-items: center;
  gap: 8px;

  i {
    font-size: 14px;
  }
}

.resource-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.indicator-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.indicator {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.indicator-label {
  font-size: 12px;
  color: #a0a0a0;
  min-width: 80px;
}

.indicator-value {
  font-size: 12px;
  font-weight: 500;

  &.tech {
    color: #4fc3f7;
    background: rgba(79, 195, 247, 0.1);
    padding: 2px 8px;
    border-radius: 4px;
  }
}

.policy-grid {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.macro-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.macro-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;

  i {
    font-size: 14px;
    color: #d4a84b;
    width: 20px;
    text-align: center;
  }

  &.secret {
    i {
      color: #8b3a3a;
    }
  }
}

.macro-label {
  font-size: 11px;
  color: #a0a0a0;
  flex: 1;
}

.macro-value {
  font-size: 14px;
  font-weight: bold;
  color: #e0e0e0;
}

.hidden-purpose {
  margin-top: 10px;
  padding: 8px;
  background: rgba(139, 58, 58, 0.2);
  border: 1px solid #8b3a3a;
  border-radius: 4px;
  font-size: 12px;
  color: #ff6b6b;

  i {
    margin-right: 6px;
  }
}
