
.social-system {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #666;

  i {
    font-size: 48px;
    margin-bottom: 12px;
    opacity: 0.5;
  }

  p {
    margin: 0;
    font-size: 14px;
  }
}

.faction-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.faction-card {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 6px;
  border: 1px solid #3a3a3a;
  overflow: hidden;
}

.faction-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background: rgba(212, 168, 75, 0.15);
  border-bottom: 1px solid #3a3a3a;
}

.faction-name {
  font-size: 14px;
  font-weight: 600;
  color: #d4a84b;
}

.faction-population {
  font-size: 12px;
  color: #a0a0a0;

  i {
    margin-right: 4px;
  }
}

.faction-body {
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.faction-ideology,
.faction-leader {
  font-size: 12px;
  color: #c0c0c0;

  i {
    width: 16px;
    margin-right: 6px;
    color: #a0a0a0;
  }
}

.faction-relation {
  margin-top: 4px;
  padding-top: 8px;
  border-top: 1px solid #333;
}

.relation-header {
  display: flex;
  justify-content: space-between;
  font-size: 11px;
  margin-bottom: 4px;

  span:first-child {
    color: #888;
  }
}

.relation-value {
  font-weight: 600;

  &.excellent {
    color: #4caf50;
  }
  &.good {
    color: #8bc34a;
  }
  &.neutral {
    color: #9e9e9e;
  }
  &.poor {
    color: #ff9800;
  }
  &.hostile {
    color: #f44336;
  }
}

.relation-bar {
  height: 6px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 3px;
  position: relative;
  overflow: hidden;
}

.relation-fill {
  height: 100%;
  transition: width 0.3s ease;
}

.relation-center {
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 100%;
  background: #666;
  transform: translateX(-50%);
}

.relation-status {
  margin-top: 6px;
  text-align: right;
}

.status-tag {
  font-size: 10px;
  padding: 2px 8px;
  border-radius: 10px;
  font-weight: 500;

  &.excellent {
    background: rgba(76, 175, 80, 0.2);
    color: #4caf50;
  }
  &.good {
    background: rgba(139, 195, 74, 0.2);
    color: #8bc34a;
  }
  &.neutral {
    background: rgba(158, 158, 158, 0.2);
    color: #9e9e9e;
  }
  &.poor {
    background: rgba(255, 152, 0, 0.2);
    color: #ff9800;
  }
  &.hostile {
    background: rgba(244, 67, 54, 0.2);
    color: #f44336;
  }
}
