
.resource-bar {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 8px;
  border: 1px solid #333;
}

.resource-header {
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 6px;

  i {
    font-size: 12px;
  }
}

.resource-label {
  font-size: 11px;
  color: #a0a0a0;
  flex: 1;
}

.resource-value {
  font-size: 11px;
  font-weight: 600;
  color: #e0e0e0;
}

.bar-container {
  height: 8px;
  background: #1a1a1a;
  border-radius: 4px;
  overflow: hidden;
}

.bar-fill {
  height: 100%;
  border-radius: 4px;
  transition:
    width 0.3s ease,
    background 0.3s ease;
  box-shadow: 0 0 8px currentColor;
}
